.slider-labels{
    display: flex;
    justify-content: space-between;
}
.slider {
    width: 100%;
    -webkit-appearance: none;
    width: 100%;
    border-radius: 5px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
}
.slider:hover{
    opacity: 1;
}
///Slider emoji change 

//MODERATE
.slider--moderate::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    border: 0;
    border-radius: 50%;
    background-image: url('../../assets/images/moderate.png');
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    width: 20px;
    height: 20px;
    cursor: pointer;
  }

  .slider::-moz-range-thumb {
    width: 23px;
    height: 25px;
    border: 0;
    background: url('../../assets/images/moderate.png');
    cursor: pointer;
  }
  
