body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background-color:#fffefd}main{display:grid;grid-template-columns:repeat(6, 1fr);grid-template-rows:repeat(8, 1fr);height:100vh}
.title{grid-area:2/2/2/6;font-weight:600}
.lift{grid-area:3/2/3/6}
.weightcontainer .weight__input{display:flex;align-items:center;height:50%}.weightcontainer .weight__input .input{width:75%;height:50%}.weightcontainer .weight__input .input__kgs{background-color:#1f1f1f;height:50%;color:#fffefd;padding:1px 2px;flex:1 1;display:flex;align-items:center;justify-content:center}

.slider-labels{display:flex;justify-content:space-between}.slider{width:100%;-webkit-appearance:none;width:100%;border-radius:5px;background:#d3d3d3;outline:none;opacity:.7;transition:opacity .2s}.slider:hover{opacity:1}.slider--moderate::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;border:0;border-radius:50%;background-image:url(/JosieAilsa/react-powerlifting-tracker.git/static/media/moderate.b9baba29.png);background-size:contain;background-position:center center;background-repeat:no-repeat;width:20px;height:20px;cursor:pointer}.slider::-moz-range-thumb{width:23px;height:25px;border:0;background:url(/JosieAilsa/react-powerlifting-tracker.git/static/media/moderate.b9baba29.png);cursor:pointer}.slider--easy::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;border:0;border-radius:50%;background-image:url(/JosieAilsa/react-powerlifting-tracker.git/static/media/easy.bc5aa1f7.png);background-size:contain;background-position:center center;background-repeat:no-repeat;width:20px;height:20px;cursor:pointer}.slider--very-easy::-moz-range-thumb{width:23px;height:25px;border:0;background:url(/JosieAilsa/react-powerlifting-tracker.git/static/media/veryEasy.4b359e02.png);cursor:pointer}.slider--very-easy::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;border:0;border-radius:50%;background-image:url(/JosieAilsa/react-powerlifting-tracker.git/static/media/veryEasy.4b359e02.png);background-size:contain;background-position:center center;background-repeat:no-repeat;width:20px;height:20px;cursor:pointer}.slider--very-easy::-moz-range-thumb{width:23px;height:25px;border:0;background:url(/JosieAilsa/react-powerlifting-tracker.git/static/media/veryEasy.4b359e02.png);cursor:pointer}.slider--very-easy::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;border:0;border-radius:50%;background-image:url(/JosieAilsa/react-powerlifting-tracker.git/static/media/veryHard.c18ad77f.png);background-size:contain;background-position:center center;background-repeat:no-repeat;width:20px;height:20px;cursor:pointer}.slider--very-easy::-moz-range-thumb{width:23px;height:25px;border:0;background:url(/JosieAilsa/react-powerlifting-tracker.git/static/media/veryHard.c18ad77f.png);cursor:pointer}
.btn{margin:5vmin}
.form{grid-area:3/2/5/6}
.nav{background-color:#1f1f1f;grid-area:1/7/1/1;display:flex;align-content:flex-end;justify-content:space-around;align-items:center}.nav__logo{background-color:#e4572e;padding:2.5vmin 2.5vmin;font-weight:700;font-size:1.5em;color:#fffefd}@media only screen and (min-width: 600px){.nav__hamburger{display:none}}.nav__container{display:flex;width:35vw;list-style-type:none;justify-content:space-between;color:#fffefd;font:"Raleway",sans-serif}@media only screen and (max-width: 600px){.nav__container{display:none}}.nav .nav__link{font-weight:600;font-size:1.25em;color:#fffefd}
.card-container{grid-area:3/2/8/6}

.liftselect{width:50%;height:50%}
.filters{grid-area:2/2/2/6;display:flex;flex-direction:row;align-items:center;justify-content:space-around}.filters .difficulty-container{width:50%}.filters .difficulty-container .difficulty-filter{width:100%}
